@mixin lib-icon-svg {
	background-image: url(asset('sprite-lib-icon-90af91bb.svg'));
	background-repeat: no-repeat;
	background-size: em(883) em(13629);
	content: "";
	display: block;
	font-size: em(16);
}

@mixin lib-icon-svg-inline {
	display: inline-block;
	position: relative;
	vertical-align: middle;
}

.lib-icon {
	display:inline-block;

	&::before {
		@include lib-icon-svg;
	}
}

.lib-icon--inline {
	&::before {
		@include lib-icon-svg-inline;
	}
}

